              color="success" 
              class="white--text"
              large
              :to="'/info'">Aprende Más</v-btn>
            </v-flex>
          </v-layout>
        </v-layout>
      </v-container>
    </v-parallax>
    <v-layout row wrap>
      <v-flex xs12>
        <v-layout row wrap
        text-xs-center  
        class="mb-5"
        >
          <v-flex xs12 class="my-2">
            <h1 class="display-2 font-weight-thin">Nuestros Clientes</h1>
          </v-flex>
          <v-flex xs12 class="my-2">
            <h1 class="display-5 font-weight-thin">Exicon se especializa en ayudar a compañías basadas en América Latina y el Caribe.</h1>
