            class="carousel"
            slot-scope="{ hover }"
            :class="`elevation-${hover ? 12 : 2}`">
              <v-carousel-item
                v-for="(client,i) in clients"
                :key="i"
                :src="require('@/assets/' + client.img + '.jpg')"
                reverse-transition="fade"
                transition="fade"
              >
