                </div>
              </v-carousel-item>
            </v-carousel>
          </v-hover>
        </v-layout>
      </v-flex>
    </v-layout>
    <v-layout row wrap
    >
      <v-flex xs12>
        <v-layout row wrap 
        text-xs-center
        justify-center            
        class="ma-5">
          <h1 class="display-2 font-weight-thin my-2">Nuestros servicios</h1>
          <h1 class="display-5 font-weight-thin my-2">Ayudamos a las empresas a lograr sus objetivos identificando socios estratégicos o inversores internacionales para una adquisición total o parcial de los activos de la compañía.</h1>
          <v-btn
          :to="'/contact'">Contáctenos</v-btn>
        </v-layout>
      </v-flex>
      <v-flex xs12>
        <v-container
          fluid
          grid-list-lg
        >
          <v-layout row wrap>
            <v-flex xs12 sm6>
              <v-hover>
                <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`">
                  <v-img
                    class="white--text"
                    height="200px"
                    :src="require('@/assets/handshake.jpg')"
                  >
                    <v-container fill-height fluid>
                      <v-layout fill-height>
                        <v-flex xs12 align-end flexbox>
                          <span class="headline">Fusiones y Adquisiciones</span>
                        </v-flex>
                      </v-layout>
                    </v-container>
                  </v-img>
                  <v-card-title>
                    <div>
                      <span><strong>Adquisiciones totales o parciales: </strong> consolidación de la industria, nueva entrada en el mercado &amp; modernizar el negocio familiar.</span>
                    </div>
                  </v-card-title>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn 
                    flat
                    class="success--text"
                    :to="'/services'"
                    >Aprende Más</v-btn>
                  </v-card-actions>
                </v-card>
              </v-hover>
            </v-flex>
            <v-flex xs12 sm6>
              <v-hover>
                <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`">
                  <v-img
                    class="white--text"
                    height="200px"
                    :src="require('@/assets/singapore.jpg')"
                  >
                    <v-container fill-height fluid>
                      <v-layout fill-height>
                        <v-flex xs12 align-end flexbox>
                          <span class="headline">Aumento de Capital</span>
                        </v-flex>
                      </v-layout>
                    </v-container>
                  </v-img>
                  <v-card-title>
                    <div>
                      <span><strong>Acceda a los mercados financieros mundiales:</strong> inversión de crecimiento, reducción de costos, &amp; operaciones de modernización.</span>
                    </div>
                  </v-card-title>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn 
                    flat
                    class="success--text"
                    :to="'/services'"
                    >Aprende Más</v-btn>
                  </v-card-actions>
                </v-card>
              </v-hover>
            </v-flex>
          </v-layout>
