    >
      <v-layout
        justify-center
        row
        wrap
      >
        <v-flex
          accent
          pt-2
          text-xs-center
          black--text
          xs12
        >
          <p class="mx-3">Logre sus objetivos estratégicos y financieros con <strong>Exicon Investment Banking</strong></p>
        </v-flex>
      </v-layout>
    </v-footer>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      clients: [
        {
          name: 'Sonda (Chile)',
          img: 'sonda',
          sector: 'Info-Tec'
        },
        {
          name: 'Terpel (Colombia)',
          img: 'terpel',
          sector: 'Petróleo y Gas'
        },
        {
          name: 'Falabella (Chile)',
          img: 'falabella',
          sector: 'Al por Menor'
        }
      ]
    }
  }
}
</script>

<style scoped>
  .carousel {
    height: 200px;
    width: 80%;
  }

  .client {
    position: absolute;
    bottom: 10px;
    background-color: rgba(0,0,0,0.5);
    color: white;
    font-size: 1.5em;
    padding: 20px;
  }
</style>






