  <v-container fluid>
    <v-parallax 
    :src="require('@/assets/compass.jpg')"
    aspect-ratio="1.7"
    height="300"
    class="my-2">
      <v-layout align-center justify-space-around row wrap fill-height>
        <h1 class="display-3 font-weight-thin white--text ma-2">我们的服务</h1>
      </v-layout>
    </v-parallax>
    <v-layout row wrap>
      <v-flex class="my-4">
        <v-layout row wrap 
        text-xs-center 
        justify-center
        class="my-2">
          <h1 class="display-2 font-weight-thin my-2">并购和融资</h1>
        </v-layout>
        <v-layout row wrap
        text-xs-center 
        justify-center
        class="my-2">
          <h1 class="display-5 font-weight-thin">我们专注于超过3000万美元的交易规模</h1>
        </v-layout>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex sm12 md6>
        <v-container
          fluid
          grid-list-lg
        >
          <v-layout row wrap>
            <v-flex xs12>
              <v-hover>
                <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`">
                  <v-parallax
                  :src="require('@/assets/handshakecolor.jpg')"
