                              <h2 class="display-5 font-weight-thin white--text">公司的全部或部分收购，合并或出售。</h2>
                            </div>
                          </v-flex>
                        </v-layout>
                      </v-layout>
                    </v-container>
                  </v-parallax>

                  <v-card-title primary-title>
                    <div>
                      <div class="headline ml-2">差异化的战略和战术建议以及有效的执行：
                        <v-spacer></v-spacer>
                        <v-btn flat  @click="showMAD = !showMAD">
                          <div>{{ showMAD ? '阅读更少' : '阅读更多' }}</div>
                        </v-btn>
                        <v-btn icon @click="showMAD = !showMAD">
