                              :key="i"
                              avatar
                              class="my-2"
                            >
                              <v-list-tile-avatar>
                                <v-icon color="accent">{{maDetail.icon}}</v-icon>
                              </v-list-tile-avatar>

                              <v-list-tile-content>
                                <v-list-tile-title>{{maDetail.title}}</v-list-tile-title>
                                <v-list-tile-sub-title>{{maDetail.subtitle}}</v-list-tile-sub-title>
                              </v-list-tile-content>
                            </v-list-tile>
                          </template>
                        </v-list>
                      </v-slide-y-transition>
                    </div>
                  </v-card-title>
                  
                  <v-card-actions>
                    <v-layout 
                    align-center 
                    justify-center 
                    row wrap 
                    fill-height
                    class="mb-2">
                      <v-btn 
                      color="accent"
                      class="white--text"
                      :to="'/contact'">联系我们</v-btn>
                    </v-layout>
                  </v-card-actions>
                </v-card>
              </v-hover>
            </v-flex>
          </v-layout>
        </v-container>
      </v-flex>
      <v-flex sm12 md6>
        <v-container
          fluid
          grid-list-lg
        >
          <v-layout row wrap>
            <v-flex xs12>
              <v-hover>
                <v-card
                slot-scope="{ hover }"
                :class="`elevation-${hover ? 12 : 2}`">
                  <v-parallax
                  :src="require('@/assets/singaporecolor.jpg')"
                  >
                    <v-container fill-height>
                      <v-layout align-center>
                        <v-layout row wrap>
                          <v-flex xs12>
                            <div class="shadow">
                              <h1 class="display-2 font-weight-thin white--text">资本筹集</h1>
                              <h2 class="display-5 font-weight-thin white--text">直接私募股权，信贷或混合融资解决方案 </h2>
                            </div>
                          </v-flex>
                        </v-layout>
                      </v-layout>
                    </v-container>
                  </v-parallax>

