                    <div>
                      <div class="headline ml-2">我们依靠丰富的复杂交易执行经验来帮助实现您的目标，包括：
                        <v-spacer></v-spacer>
                        <v-btn flat  @click="showCRD = !showCRD">
                          <div>{{ showCRD ? '阅读更少' : '阅读更多' }}</div>
                        </v-btn>
                        <v-btn icon @click="showCRD = !showCRD">
                          <v-icon>{{ showCRD ? 'keyboard_arrow_up' : 'keyboard_arrow_down' }}</v-icon>
                        </v-btn>
                      </div>
                      <v-slide-y-transition>
                        <v-list three-line
                        v-show="showCRD">
                          <template v-for="(crDetail, i) in crDetails">

                            <v-list-tile
                              :key="i"
                              avatar
                              class="my-2"
                            >
                              <v-list-tile-avatar>
                                <v-icon color="accent">{{crDetail.icon}}</v-icon>
                              </v-list-tile-avatar>

                              <v-list-tile-content>
                                <v-list-tile-title>{{crDetail.title}}</v-list-tile-title>
                                <v-list-tile-sub-title>{{crDetail.subtitle}}</v-list-tile-sub-title>
                              </v-list-tile-content>
                            </v-list-tile>
                          </template>
                        </v-list>
                      </v-slide-y-transition>
                    </div>
                  </v-card-title>
                  
                  <v-card-actions>
                    <v-layout 
                    align-center 
                    justify-center 
                    row wrap 
                    fill-height
                    class="mb-2">
                      <v-btn 
                      color="accent"
                      class="white--text"
                      :to="'/contact'">联系我们</v-btn>
                    </v-layout>
                  </v-card-actions>
                </v-card>
              </v-hover>
            </v-flex>
          </v-layout>
        </v-container>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    data () {
      return {
        showMAD: true,
        showCRD: true,
        maDetails: [
          {
            icon: 'assignment_turned_in',
            title: '交易评估',
            subtitle: '我们评估潜在目标，提供估值分析，评估和提出财务和战略选择。'
