          {
            icon: 'autorenew',
            title: '资本重组',
            subtitle: '公司股权资本重组/债务再融资，主权债务再融资，上市公司的私人投资(“PIPEs”)。'
          }
        ]
      }
    }
  }
</script>

<style scoped>
  .shadow {
    position: absolute;
    top: 30%;
    left: 0;
    background-color: rgba(0,0,0,0.5);
    color: white;
    padding: 20px;
  }
</style>



