        <v-layout row wrap text-xs-center 
        justify-center
        class="my-2">
          <h1 class="display-2 font-weight-thin">我们的服务焦点</h1>
        </v-layout>
        <v-layout row wrap
        text-xs-center 
        justify-center
        class="my-2">
          <h1 class="display-5 font-weight-thin">我们的成功来自于在我们熟知的行业和地区合作合适的客户类型。</h1>
        </v-layout>
      </v-flex>
      <v-flex xs12 sm6 md4
      class="my-2 px-2">
        <v-hover>
          <v-card
          slot-scope="{ hover }"
          :class="`elevation-${hover ? 12 : 2}`">
            <v-img
            :src="require('@/assets/latam.jpg')"
            height="250px"
            contain
