            <v-img
            src="https://www.fotografisanje.rs/wp-content/uploads/2013/10/01-3-400x267.jpg"
            height="250px"
            contain
            >
            </v-img>

            <v-card-title primary-title>
              <div>
                <div class="headline">我们比较喜欢具有差异化产品行业。</div>
