            avatar: 'mario',
            name: 'Mario Lizana-Allende',
            title: '常务董事',
            excerpt: '2003年成立Exicon',
            content: 'Mario Lizana-Allende在美国迈阿密担任KPMG LLP拉丁美洲和加勒比地区经理后创立了Exicon。他拥有多年的企业融资经验并且与美国，欧洲和亚洲最大的金融投资者有密切的关系。Mario拥有马德里康普顿斯大学的法律学位，马德里的I.A.D.E.的国际商业管理硕士学位，并且是马德里José Ortega和Gasset基金会的博士候选人。他也是前美国智利商会会长，前智利哥伦比亚商会创始委员会主席，现任哥伦比亚新加坡商会创始委员会主席。他说流利的西班牙语，德语和英语。'
          },
          {
            avatar: 'cecil',
            name: 'Cecil Yi Peng Ang (洪毅澎)',
            title: '董事',
            excerpt: '2018年加入Exicon',
