    </v-parallax>
    <v-layout row wrap
    text-xs-center
    justify-center>
      <v-flex xs12
      class="my-4">
        <v-layout row wrap text-xs-center 
        justify-center
        class="my-2">
          <h1 class="display-2 font-weight-thin">Nuestro Objetivo</h1>
        </v-layout>
        <v-layout row wrap
