            avatar: 'mario',
            name: 'Mario Lizana-Allende',
            title: 'Director General',
            excerpt: 'Fundó Exicon en 2003',
            content: 'Mario Lizana-Allende fundó Exicon después de haber sido gerente para América Latina y el Caribe de KPMG LLP en Miami, Estados Unidos. Trae consigo años de experiencia en finanzas corporativas y relaciones con los mayores inversores estratégicos y financieros de los Estados Unidos, Asia y Europa. Mario es Licenciado en Derecho por la Universidad Complutense de Madrid, Máster en Dirección de Empresas Internacionales por la I.A.D.E. de Madrid, y Candidato a Doctor por la Fundación José Ortega y Gasset de Madrid. Se desempeñó como Presidente de la Cámara de Comercio Chile-Estados Unidos en los Estados Unidos y fue Presidente del Comité Fundador de la Cámara de Comercio de Colombo Chilena, y actualmente es el Presidente del Comité Fundador de la Cámara de Comercio de Colombo Singapur en Colombia. Habla con fluidez español, alemán e inglés.'
          },
          {
            avatar: 'cecil',
            name: 'Cecil Yi Peng Ang',
            title: 'Director',
            excerpt: 'Se unió a Exicon en 2018',
            content: 'Cecil se unió a Exicon después de fundar Betafish Pte Ltd, una compañía de tecnología financiera en Singapur y Silicon Valley, California, EE. UU. Antes de Betafish, Cecil pasó cinco años como asociado sénior de investigación en Sands Capital Management LLC (EE. UU.), Una empresa de inversión que gestiona más de USD 100B. Cecil se graduó con la más alta distinción en Economía y Finanzas de la Universidad de Virginia, EE. UU. Y es Candidato de Analista Financiero Colegiado Nivel III. Actualmente también es el Secretario General del Comité Fundador de la Cámara de Comercio de Colombo Singapur en Colombia. Habla inglés, mandarín y español con fluidez.'
          }
        ]
      }
